import React,{useEffect, useState} from 'react'
import axios from 'axios'
import { } from 'bootstrap'
import {Line} from 'react-chartjs-2'

const Stats=()=>{
    const [data,setData]= useState([])
    const [active,setActive]= useState('')
    const [confirmed,setConfirmed]= useState('')
    const [deaths,setDeaths]= useState('')
    const [recovered,setRecovered]= useState('')
    const [date,setDate]= useState(new Date())
    const [showgraph,setshowgraph] = useState('confirmed')

    const getData= async() =>{
        const res = await axios.get('https://api.covid19india.org/data.json')
        const statedata= await res.data.statewise;
        setData(statedata)
        setActive(statedata[0].active)
        setConfirmed(statedata[0].confirmed)
        setDeaths(statedata[0].deaths)
        setRecovered(statedata[0].recovered)
    }
    
    useEffect(()=>{
        getData()
    },[]);


    return(
        <div className='statstable'>
            <div className='container-fluid mb-5'>
                <h1 className='heading'>Stats</h1>
            </div>
            <div className='totaldetails'>
                <div className='confirmed' onClick={()=>setshowgraph('confirmed')}>
                    <h5>Confirmed</h5>
                    <h3>{confirmed}</h3>
                    { showgraph=='confirmed' ?
                    <Line 
                        data={{ 
                            labels: ['1', '2', '3', '4', '5', '6'],
                            datasets: [
                                {
                                label: '# of Votes',
                                data: [12, 19, 3, 5, 2, 3],
                                fill: false,
                                backgroundColor: 'rgb(255, 99, 132)',
                                borderColor: 'rgba(255, 99, 132, 0.2)',
                                },
                            ],
                        }}
                        height={300}
                        width={500}
                        options={{
                            maintainAspectRatio:true
                        }}
                    />
                    :
                    null
                }
                </div>
                <div className='active' onClick={()=>setshowgraph('Active')}>
                    <h5> Active </h5>
                    <h3>{active}</h3>
                    { showgraph=='Active' ?
                    <Line 
                        data={{ 
                            labels: ['1', '2', '3', '4', '5', '6'],
                            datasets: [
                                {
                                label: '# of Votes',
                                data: [12, 19, 3, 5, 2, 3],
                                fill: false,
                                backgroundColor: 'rgb(255, 99, 132)',
                                borderColor: 'rgba(255, 99, 132, 0.2)',
                                },
                            ],
                        }}
                        height={300}
                        width={500}
                        options={{
                            maintainAspectRatio:true
                        }}
                    />
                    :
                    null
                }
                </div>
                <div className='recovered' onClick={()=>setshowgraph('recovered')}>
                    <h5>Recovered</h5>
                    <h3>{recovered}</h3>
                    { showgraph=='recovered' ?
                    <Line 
                        data={{ 
                            labels: ['1', '2', '3', '4', '5', '6'],
                            datasets: [
                                {
                                label: '# of Votes',
                                data: [12, 19, 3, 5, 2, 3],
                                fill: false,
                                backgroundColor: 'rgb(255, 99, 132)',
                                borderColor: 'rgba(255, 99, 132, 0.2)',
                                },
                            ],
                        }}
                        height={300}
                        width={500}
                        options={{
                            maintainAspectRatio:true
                        }}
                    />
                    :
                    null
                }
                </div>
                <div className='death' onClick={()=>setshowgraph('Death')}>
                    <h5>Death</h5>
                    <h3>{deaths}</h3>
                    { showgraph=='Death' ?
                    <Line 
                        data={{ 
                            labels: ['1', '2', '3', '4', '5', '6'],
                            datasets: [
                                {
                                label: '# of Votes',
                                data: [12, 19, 3, 5, 2, 3],
                                fill: false,
                                backgroundColor: 'rgb(255, 99, 132)',
                                borderColor: 'rgba(255, 99, 132, 0.2)',
                                },
                            ],
                        }}
                        height={300}
                        width={500}
                        options={{
                            maintainAspectRatio:true
                        }}
                    />
                    :
                    null
                }
                </div>
            </div>
            <div className='table-responsive'>
                <table className='table table-hover'>
                    <thead className='thead-black'>
                        <tr>
                            <th>State</th>
                            <th>Confirmed</th>
                            <th>Recovered</th>
                            <th>Death</th>
                            <th>Active</th>
                            <th>Updated</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            data.map((item,index)=>{
                                return(
                                <tr key={index}>
                                    <th style={{textAlign:'start'}}>{item.state} ({item.statecode})</th>
                                    <td>{item.confirmed}</td>
                                    <td>{item.recovered}</td>
                                    <td>{item.deaths}</td>
                                    <td>{item.active}</td>
                                    <td>{item.lastupdatedtime.split('/')[0]>=date.getDate()-1 ? `${date.getHours()-item.lastupdatedtime.split('')[12]} Hours ago` : 'More Than day ago'}</td>
                                </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}
export default Stats